.dialogsColumns {
  display: grid;
  grid-template-columns: 3fr 6fr;
  font-family: serif;
  background-color: rgb(252, 252, 252);
  line-height: 250%;
  padding-top: 15px;
  padding-left: 10px;
  color: rgb(142, 9, 9);
  text-align: center;
}
.dialogsColumns img {
  width: 70px;
border-radius: 35px;

}
.dialogs {
  background: linear-gradient(rgb(252, 252, 252), rgb(120, 2, 2)); 
}

.messages {
  padding-left: 10px;
}

.dialogs .active {
  color: rgb(142, 9, 9);
  font-size: large;
  }
